ymaps.ready(init)

const coord = [59.858363, 30.319875]
const restaurants = [
    {
        name: 'restaurant',
        coord: [59.898363, 30.319875]
    },
    {
        name: 'restaurant2',
        coord: [59.858063, 30.369875]
    },
    {
        name: 'restaurant3',
        coord: [59.808663, 30.319875]
    },
]

maps = ['map1','map3','map4']

function init() {
    maps.forEach(map => {
        let myMap = new ymaps.Map(map, {
            center: coord,
            zoom: 11,
            controls:[]
        });

        let placemark = new ymaps.Placemark(coord, {},
            {
                iconLayout: 'default#image',
                iconImageHref: "img/mark.svg",
                iconImageSize: [18, 18],
            }
        );
        myMap.geoObjects.add(placemark);
    })

    let myMap2 = new ymaps.Map("map2", {
        center: coord,
        zoom: 11,
        controls:[]
    });

    restaurants.forEach(restaurant => {

        let placemark = new ymaps.Placemark(restaurant.coord, {},
            {
                iconLayout: 'default#image',
                iconImageHref: "img/mark2.svg",
                iconImageSize: [23, 23],
            }
        );
        myMap2.geoObjects.add(placemark);
    })
}
