$(".burger").click(function () {
    $(".header").addClass("_open")
    $(".page").addClass("fixed-body")
})

$("._close-menu").click(function () {
    $(".header").removeClass("_open")
    $(".page").removeClass("fixed-body")
})

$("._open-search").click(function () {
    $(".header").toggleClass("_search")
    $(".page").toggleClass("fixed-body")
})
