function mainSlider() {
    var swiper = new Swiper('.main .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        loop: true,
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
    })
}

function promoSlider() {
    var swiper = new Swiper('.promo .swiper-container', {
        slidesPerView: 3,
        spaceBetween: 40,
        navigation: {
            nextEl: '.promo .swiper-button-next',
            prevEl: '.promo .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 'auto',
                spaceBetween: 20,
            },
            768: {
                slidesPerView: 'auto',
                spaceBetween: 20,
            },
            1200: {
                slidesPerView: 3,
                spaceBetween: 40,
            },
        }
    })
}

mainSlider()
promoSlider()
